import React, { Component } from 'react';

class PlayerBar extends Component {
  render() {
    return (
    <section className="player-bar">
      <section id="buttons">
        <button className="audio-button" id="previous" onClick={this.props.handlePrevClick}>
          <span className="icon ion-md-skip-backward"></span>
        </button>
        <button className="audio-button" id="play-pause"
        onClick={this.props.handleSongClick}
        >
          <span className={this.props.isPlaying ?
            //if song is playing, show pause icon
            "icon ion-md-pause" :
            //if song is not playing, show play icon
            "icon ion-md-play"}>
          </span>
        </button>
        <button className="audio-button" id="next" onClick={this.props.handleNextClick}>
          <span className="icon ion-md-skip-forward"></span>
        </button>
      </section>
      <section id="time-control">
        <div className="current-time">{this.props.formatTime(this.props.currentTime)}</div>
        <input
          type="range"
          className="seek-bar"
          value={(this.props.currentTime / this.props.duration) || 0}
          max="1"
          min="0"
          step="0.01"
          onChange={this.props.handleTimeChange}
        />
        <div className="total-time">{this.props.formatTime(this.props.duration)}</div>
      </section>
      <section id="volume-control">
        <div className="icon ion-md-volume-low"></div>
        <input
        type="range"
        className="seek-bar"
        value={this.props.currentVol}
        max="1"
        min="0"
        step="0.01"
        onChange={this.props.handleVolChange}/>
        <div className="icon ion-md-volume-high"></div>
      </section>
    </section>
    );
  }
}

export default PlayerBar;
